@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin roboto-condenced($weight: normal, $size: 14px, $height: 16px) {
  font-family: "roboto-condenced", "Arial", sans-serif;
  font-size: $size;
  line-height: $height;
  font-weight: $weight;
}

@mixin footer-text {
  font-size: 15px;
  line-height: 25px;
  font-weight: 400;
  color: rgba($footer-text-color, 0.5);
}

@mixin interactive {
  &:hover {
    opacity: 0.3;
    transition: opacity 0.2s ease;
  }

  &:active {
    opacity: 0.1;
    transition: opacity 0.2s ease;
  }
}

@mixin form-interactive {
  &:hover {
    background-color: rgba($light-bg-color, 0.5);
    transition: background-color 0.2s ease;
  }

  &:focus {
    background-color: rgba($light-bg-color, 0.3);
    transition: background-color 0.2s ease;
  }
}
